  width: -moz-fit-content;
  width: fit-content;
  font-size: 16px;
  background-color: white;
  border: 3px solid rgb(152, 152, 48);
  border-radius: 5px;
  padding: 1vh 3vh;
  transition: 0.3s all linear;
}
.button:hover {
  background-color: rgb(229, 229, 73);
}
.button:active {
  background-color: rgb(152, 152, 48);
}

.button_active {
  background-color: rgb(152, 152, 48);
}

.header {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 5vh;
  margin: 10px;
}

section {
  background-color: white;
  border: 1px solid brown;
  border-radius: 5px;
}

.main {
  position: relative;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(2, auto);
  grid-gap: 1vh;
  gap: 1vh;
  margin: 10px;
}

.block1 {
  grid-column: span 4;
}

.block2 {
  grid-row: span 3;
}

.block3 {
  grid-area: span 2/span 2;
}

.block4 {
  grid-area: span 2/span 2;
}

.block__content {
  margin: 0 2vh;
}

@media (max-width: 650px) {
  .block__content#level {
    margin: 0 0;
  }
}

.task__header {
